
import ViewBox from 'vux/src/components/view-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Tabbar from "@/components/Tabbar";
export default {
name: "mine",
components: {
ViewBox,
Tabbar,
Cell,
Group,
XHeader
},
created () {
this.queryUser();
},
data() {
return {
userInfo: {}
};
},
computed: {
idCardStatus: function () {
if (this.userInfo.isAuditIdCard === -1) {
return '(未提交)';
} else if (this.userInfo.isAuditIdCard === 0) {
return '(审核中)';
} else if (this.userInfo.isAuditIdCard === 1) {
return '(审核通过)';
} else if (this.userInfo.isAuditIdCard === 2) {
return '(审核未通过)';
}
}
},
methods: {
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
console.log(data);
this.userInfo = data.data.user;
}
});
},
goQrcode() {
if (this.userInfo.vipLevel === -1) {
return this.$vux.toast.text("不能进入我的推荐码页面，请先申请拿一件货以上，才能进行推荐", "top");
}
this.$router.push('/qrcode');
}
}
};



